const dict = {
  '(name)': '',
  '(desc)': '',
  '(cate)': '',
  '(shop)': '',
  '(show)': '',
  '(price|sale)': '',
  '(url|link)': '',
  '(product)': '',
  '(hot)': '',
  '(order)': '',
  '(image|img|pic)': '',
  '(state|status)': '',
  '(start)': '',
  '(end)': '',
  '(time|datetime)': '',
  '(date)': '',
  '(range)': '',
  '(focus)': '',
  '(comment)': '',
  '(num)': '',
  '(color)': '',
  '(game)': '',
  '(pay|buy)': '',
  '(amt|amount)': '',
  '(detail)': '',
  '(click)': '',
  '(rank)': '',
};
const getTitle = key => {
  for (var rule in dict) {
    if (new RegExp(rule, 'i').test(key)) {
      return dict[rule];
    }
  }
  return key;
};
const getType = function(data) {
  if (typeof data == 'object') {
    return toString.call(data) === '[object Array]' ? 'array' : 'object';
  } else {
    return typeof data;
  }
};
const dataHandle = {
  number: key => ({
    type: 'number',
    format: 'number',
    title: getTitle(key),
    options: {
      grid_columns: 3,
    },
  }),
  boolean: key => ({
    type: 'boolean',
    title: getTitle(key),
    options: {
      grid_columns: 3,
    },
  }),
  string: function(key, data) {
    const schema = {};
    schema.type = 'string';
    schema.title = getTitle(key);
    schema.format = 'text';
    schema.options = {
      grid_columns: 3,
    };

    switch (true) {
      case /\.(jpe?g|png|gif)$/.test(data):
        schema.format = 'upload';
        schema.links = [
          {
            rel: '',
            href: '{{self}}',
            mediaType: 'image',
          },
        ];
        break;

      case /\.(exe|swf|zip|rar|docx?|pptx?|xlsx?)$/.test(data):
        schema.format = 'upload';
        schema.download = true;
        break;

      case /\.mp4$/.test(data):
        schema.format = 'upload';
        schema.links = [
          {
            rel: '',
            href: '{{self}}',
            mediaType: 'video/mp4',
          },
        ];
        break;

      case /^[12]\d{3}-\d{2}-\d{2} [\d:]{8}$/.test(data):
        schema.format = 'datetime-local';
        schema.options.flatpickr = {
          wrap: true,
          showClearButton: true,
          time_24hr: true,
          allowInput: false,
        };
        break;

      default:
        schema.format = 'text';
        break;
    }
    return schema;
  },
  object: function(key, data, opts = {}) {
    const schema = {};
    schema.title = getTitle(key);
    schema.type = 'object';
    schema.format = 'grid';
    schema.properties = {};
    schema.options = opts;
    for (var k in data) {
      schema.properties[k] = dataHandle[getType(data[k])](k, data[k]);
    }
    return schema;
  },
  array: function(key, data, opts = {}) {
    const schema = {};
    schema.type = 'array';
    schema.title = getTitle(key);
    schema.items = {};
    schema.options = opts;
    if (data[0] !== undefined) {
      schema.items = dataHandle[getType(data[0])](key, data[0]);
    }
    return schema;
  },
};

export default (json, name = '') => {
  try {
    const data = typeof json == 'string' ? JSON.parse(json) : json;
    return dataHandle[getType(data)](name, data, { disable_collapse: true });
  } catch (err) {
    console.warn(err);
    return {
      title: '',
      properties: {},
    };
  }
};
