import React from 'react';
import { Wrap, Table } from '../../components';

export const TableList = props => {
  const host = process.env.NODE_ENV === 'production' ? window.location.host : 'localhost:8081';
  const apiPrefix = `//${host}/_lego_api_/`;
  const config = {
    base: { name: '', api: apiPrefix + 'table/list?name={{name}}&pn={{pageNo}}', path: 'data.list' },
    cols: [
      { key: 'id', name: 'ID', width: '100', fn: [] },
      { key: 'name', name: '', width: '300', fn: ['search'] },
      { key: 'desc', name: '', width: '', fn: [] },
    ],
    handles: [
      {
        key: 'preview',
        name: '',
        icon: 'eye',
        url: '/table/use/{{id}}',
        action: 'link',
      },
      {
        key: 'edit',
        name: '',
        icon: 'edit',
        url: '/table/edit/{{id}}',
        action: 'link',
      },
      { key: 'delete', name: '', icon: 'trash-alt', url: apiPrefix + 'table/delete?id={{id}}', action: 'api' },
    ],
  };

  return (
    <Wrap>
      <div className='lego-card'>
        <div id='tableTopHook' className='table-top clearfix'>
          <h2 className='title'>{config.base.name}</h2>
          <div className='btns'>
            <button
              type='button'
              className='btn btn-primary btn-sm'
              onClick={() => props.history.push('/table/create')}>
              <i className='fas fa-plus' />
              <span></span>
            </button>
          </div>
        </div>
        <Table config={config}></Table>
      </div>
    </Wrap>
  );
};
