import React, { useRef, useEffect } from 'react';
import { Wrap, SchemaForm, Button } from '../../components';
import { createChart } from '../../config/schema';
import { axios, toast } from '../../common/utils';
import { CHART, PREPATH } from '../../config/apis';

export const ChartEdit = props => {
  const isEdit = props.match.path === '/chart/edit/:id';
  const chartEditor = useRef(null);

  if (isEdit) {
    createChart.title = '';
  } else {
    createChart.title = '';
  }

  function doSubmit() {
    const editor = chartEditor.current;
    const validates = editor.validate();

    // 
    if (validates.length > 0) {
      toast(`<br />${validates.map(err => err.path + ': ' + err.message).join('<br />')}`);
      return;
    }

    // 
    const data = editor.getValue();
    const { name, desc } = data;
    const postData = {
      name,
      desc,
      config: JSON.stringify(data),
    };

    // 
    if (isEdit) {
      postData.id = props.match.params.id;
    }

    axios('POST', CHART, postData)
      .then(() => {
        toast('');
        props.history.push('/chart/list');
      })
      .catch(err => {
        toast(err.msg);
      });
  }

  useEffect(() => {
    if (isEdit) {
      const id = props.match.params.id;
      axios('GET', CHART, { id }).then(res => {
        const { config } = res.data;
        if (config) {
          chartEditor.current.setValue(JSON.parse(config));
        }
      });
    }
  }, [isEdit, props.match.params.id]);

  return (
    <Wrap>
      <div className='lego-card'>
        <SchemaForm onReady={editor => (chartEditor.current = editor)} schema={createChart} />
        <div className='btns-row'>
          <Button onClick={doSubmit} value='' extClass='btn-primary' />
          <Button value='' onClick={() => window.open(`${PREPATH}/help/chart`)} extClass='btn-outline-primary' />
          <Button onClick={() => props.history.goBack()} value='' extClass='btn-outline-secondary' />
        </div>
      </div>
    </Wrap>
  );
};
