import React, { useState, useRef, useLayoutEffect } from 'react';
import { AceCode } from '..';

export default ({ schema, onUpdate }) => {
  const [formSchema, setFormSchema] = useState(schema);
  const aceRef = useRef(null);
  const choiceRef = useRef(null);

  useLayoutEffect(() => {
    choiceRef.current = new window.Choices('#formTypeSelect', {
      position: 'bottom',
      shouldSort: false,
    });
    return () => {
      choiceRef.current.destroy();
    };
  }, []);

  function onGridChange(evt) {
    const { value } = evt.target;
    const tmp = Object.assign({}, formSchema);
    tmp.options.grid_columns = +value;
    setFormSchema(tmp);
  }

  function onTypeChange(evt) {
    const { value } = evt.target;
    const tmp = {
      title: formSchema.title,
      type: 'string',
      format: value,
      options: {
        grid_columns: formSchema.options.grid_columns,
      },
    };

    switch (value) {
      case 'number':
        tmp.type = 'number';
        break;

      case 'boolean':
        tmp.type = 'boolean';
        break;

      case 'range':
        tmp.type = 'number';
        tmp.minimum = 1;
        tmp.maximum = 100;
        tmp.step = 2;
        break;

      case 'select':
        tmp.type = 'string';
        tmp.enum = ['item1', 'item2', 'item3', 'item4'];
        tmp.options.enum_titles = ['1', '2', '3', '4'];
        break;

      case 'datetime-local':
        tmp.type = 'string';
        tmp.options.flatpickr = {
          wrap: true,
          showClearButton: true,
          time_24hr: true,
          allowInput: false,
        };
        break;

      case 'upload':
        tmp.type = 'string';
        tmp.links = [
          {
            rel: 'view',
            href: '{{self}}',
            mediaType: 'image',
          },
        ];
        break;

      case 'html':
        tmp.type = 'string';
        tmp.options.wysiwyg = true;
        break;

      default:
        tmp.type = 'string';
    }
    setFormSchema(tmp);
  }

  return (
    <div className='row'>
      <div className='col-md-6'>
        <div className='form-group'>
          <label className='form-control-label'></label>
          <select id='formTypeSelect' className='form-control' value={formSchema.format} onChange={onTypeChange}>
            <option value='text'></option>
            <option value='textarea'></option>
            <option value='number'></option>
            <option value='boolean'></option>
            <option value='password'></option>
            <option value='datetime-local'></option>
            <option value='upload'></option>
            <option value='url'></option>
            <option value='select'></option>
            <option value='range'></option>
            <option value='html'></option>
          </select>
        </div>
      </div>
      <div className='col-md-6'>
        <div className='form-group'>
          <label className='form-control-label'>Grid</label>
          <input
            className='form-control'
            type='number'
            min='3'
            max='12'
            onChange={onGridChange}
            value={formSchema.options.grid_columns}
          />
        </div>
      </div>
      <div className='col-md-12'>
        <div className='form-group' style={{ height: 200 }}>
          <AceCode type='json' code={JSON.stringify(formSchema, null, 2)} onReady={ace => (aceRef.current = ace)} />
        </div>
      </div>
      <div className='col-md-12' style={{ textAlign: 'right' }}>
        <button
          className='btn btn-sm btn-outline-primary'
          onClick={() => window.open('https://github.com/json-editor/json-editor#json-editor')}>
          
        </button>
        <button
          className='btn btn-sm btn-success'
          style={{ marginLeft: 10 }}
          onClick={() => onUpdate(JSON.parse(aceRef.current.getValue()))}>
          
        </button>
      </div>
    </div>
  );
};
